<template> 
  <div class="slide-show" @mouseover="clearInv" @mouseout="runInv">
    <div class="slide-img">
      <a :href="slideShow[nowIndex].href">
        <img :src="slideShow[nowIndex].src">
      </a>
    </div>
    <h2>{{slideShow[nowIndex].title}}</h2>
    <ul class="slide-pages">
      <li @click="goto(prev)">&lt;</li>
      <li v-for="(item, index) in slideShow" @click="goto(index)">
        <a :class="[{on:index==nowIndex}]">{{index+1}}</a>
      </li>
      <li @click="goto(netx)">&gt;</li>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        nowIndex: 0,
        isShow: true
      }
    },
    props: {
      slideShow: {
        type: Array,
        default: []
      },
      Inv:{
        type:Number,
        default:2000
      }
    },
    methods:{
      goto(index){
          this.nowIndex=index
          this.$emit('onchange',index)
      },
      runInv(){
        this.Invtime=setInterval(()=>{
          this.goto(this.netx)
        },this.Inv)
      },
      clearInv(){
        clearInterval(this.Invtime)
      }
    },
    mounted:function(){
      this.runInv()
    },
    computed:{
      prev(){
        if(this.nowIndex===0){
          return this.slideShow.length-1
        }else{
          return this.nowIndex-1
        }
      },
      netx(){
        if(this.nowIndex===this.slideShow.length-1){
          return 0
        }else{
          return this.nowIndex+1
        }
      }
    }
  }

</script>

<style scoped>
  .slide-trans-enter-active {
    transition: all .5s;
  }

  .slide-trans-enter {
    transform: translateX(900px);
  }

  .slide-trans-old-leave-active {
    transition: all .5s;
    transform: translateX(-900px);
  }

  .slide-show {
    position: relative;
    margin: 15px 15px 15px 0;
    width: 900px;
    height: 500px;
    overflow: hidden;
  }

  .slide-show h2 {
    position: absolute;
    width: 100%;
    height: 100%;
    color: #fff;
    background: #000;
    opacity: .5;
    bottom: 0;
    height: 30px;
    text-align: left;
    padding-left: 15px;
  }

  .slide-img {
    width: 100%;
  }

  .slide-img img {
    width: 100%;
    position: absolute;
    top: 0;
  }

  .slide-pages {
    position: absolute;
    bottom: 10px;
    right: 15px;
  }

  .slide-pages li {
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #fff;
  }

  .slide-pages li .on {
    text-decoration: underline;
    padding: 4px;
    color: red;

  }

</style>



