<template>
    <div class="chooser-component">
      <div>
        <ul class="chooser-list">
          <li v-for="(item,index) in versionList" @click="toggleSelection(index)" :class="{active:checkActive (index)}">{{item.label}}</li>
        </ul>
      </div>
    </div> 
</template>

<script>
// import _ from 'lodash'
// import func from './vue-temp/vue-editor-bridge';
export default {
  props: {
    versionList:{
      type: Array,
      default: [{
        label: 'test',
        value: 0
      }]
    }
  },
  data () {
    return {
      nowIndexes: [0]
    }
  },
  methods: {
    toggleSelection (index) {
      if(this.nowIndexes.indexOf(index)!==-1){
        Array.prototype.indexOf = function(val) { 
          for (let i = 0; i < this.length; i++) { 
            if (this[i] == val) return i; 
          } 
          return -1; 
        };
        Array.prototype.remove = function(val) { 
            let index = this.indexOf(val); 
            if (index > -1) { 
            this.splice(index, 1); 
            } 
        };
        this.nowIndexes.remove(index)
      }else{
        this.nowIndexes.push(index)
      }
      this.$emit('on-change',this.newDtae())
      
      
    },
    newDtae(){
      let newArr=[]
        for(let j=0; j<this.nowIndexes.length; j++){
          newArr.push(this.versionList[this.nowIndexes[j]])
      }
      return newArr
    },
    checkActive (index) {
        return this.nowIndexes.indexOf(index)!==-1
    }
  },
  mounted:function(){
    
  }

  
}
</script>

<style scoped>
.chooser-component {
  position: relative;
  display: inline-block;
}
.chooser-list li{
  display: inline-block;
  border: 1px solid #e3e3e3;
  height: 25px;
  line-height: 25px;
  padding: 0 8px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}
.chooser-list li.active {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
</style>



