<template>
    <div class="chooser-component">
      <div>
        <ul class="chooser-list">
          <li v-for="(tiem,index) in buyTypes" @click="chosenSelection(index)" :class="{active:chooserId==index}" :key="index">{{tiem.label}}</li>
        </ul>
      </div>
    </div>
</template>

<script>
export default {
  data(){
    return {
      chooserId:0,
    }
  },
  props:{
    buyTypes:{
      type:Array,
      default:[{
        label:'test',
        value:0
      }]
    }
  },
  methods:{
    chosenSelection(index){
      this.chooserId=index
      this.$emit('on-change',this.buyTypes[index])
    }
  }
}
</script>

<style scoped>
.chooser-component {
  position: relative;
  display: inline-block;
}
.chooser-list li{
  display: inline-block;
  border: 1px solid #e3e3e3;
  height: 25px;
  line-height: 25px;
  padding: 0 8px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}
.chooser-list li.active {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
</style>



