import Vue from 'vue';
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
    state:{
      tableData:[],
      Params:{}
    },
    getters:{
      getOrderList(state){
        return state.tableData
      }
    },
    mutations:{
      changeOrderList(state,getTable){
        state.tableData=getTable
        console.log(state.tableData)
      },
      changeParmas(state,getParmas){
        state.Params=getParmas
      }
    },
    actions:{
      payload({commit,state}){
        // console.log(state.Params)
        Vue.http.post('index',state.Params)
          .then((res) => {
            commit('changeOrderList',res.body.getOrderList.list)
          }, (err) => {

          })
        }
      }
  })


