<template>
  <div class="login-form">
    <div class="g-form">
      <div class="g-form-line">
        <span class="g-form-label">用户名：</span>
        <div class="g-form-input">
          <input type="text" v-model='usernameModel' placeholder="请输入用户名">
        </div>
        <span class="g-form-error">{{userError.errorText}}</span>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">密码：</span>
        <div class="g-form-input">
          <input type="password" v-model="passwordModel" placeholder="请输入密码">
        </div>
        <span class="g-form-error">{{passwordError.passwordText}}</span>
      </div>
      <div class="g-form-line">
        <div class="g-form-btn">
          <a class="button" @click="login">登录</a>
        </div>
      </div>
      <p>{{errorText}}</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      usernameModel: '',
      passwordModel: '',
      errorText: ''
    }
  },
  computed: {
    userError(){
      let errorText, state
      if(!/@/g.test(this.usernameModel)){
        errorText='名字不符合没有包含@'
        state=false;
      }else{
        errorText='';
        state=true;
      }
      if(!this.userDefault){
        errorText='';
        this.userDefault=true;
      }
      return {
        errorText,
        state
      }
    },
    passwordError(){
      let passwordText,state
      if(!/^\w{1,6}$/g.test(this.passwordModel)){
        passwordText='密码只能是1-6位';
        state=false;
      }else{
        passwordText='';
        state=true;
      }
      if(!this.passwordDefault){
        passwordText='';
        this.passwordDefault=true;
      }
      return {
        passwordText,
        state
      }
    }
  },
  methods: {
    login(){
      if(this.userError.state || this.passwordError.state){
        this.$http.get('index').then((res)=>{
          let data=res.body.login
          this.$emit('has-log',data)
        },(error)=>{
          alert('出错啦！！！'+error)
        })
      }else{
        this.errorText='部分选项验证错误'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



