import Vue from 'vue'
import Router from 'vue-router'
import Iindex from '@/pages/index'
import Detail from '@/pages/detail'
import AnsPage from '@/pages/detail/analysis'
import CountPage from '@/pages/detail/count'
import ForPage from '@/pages/detail/forecast'
import PubPage from '@/pages/detail/publish'
import OrderPage from '@/pages/orderList'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Iindex',
      component: Iindex
    },
    {
      path: '/orderList',
      name: 'OrderPage',
      component: OrderPage
    },
    {
      path: '/detail',
      name: 'Detail',
      redirect:'/',
      component: Detail,
      children:[
        {
          path: 'analysis', 
          name: 'AnsPage',
          component: AnsPage
        },
        {
          path: 'count',
          name: 'CountPage',
          component: CountPage
        },
        {
          path: 'forecast',
          name: 'ForPage',
          component: ForPage
        },
        {
          path: 'publish',
          name: 'PubPage',
          component: PubPage
        }
      ]
    }

  ]
})



