<template>
    <div class="selection-component">
      <div class="selection-show" @click="toggleDrop">
        <span >{{selectionData[selectVal].label}}</span>
        <div class="arrow"></div>
      </div>
      <div class="selection-list"  v-if="isDorp">
        <ul>
          <li v-for="(item,index) in selectionData" @click="chooseSelection(index)">{{item.label}}</li>
        </ul>
      </div>
    </div>
</template>

<script>
export default {
  data(){
    return{
      selectVal:0,
      isDorp:false
    }
  },
  props:{
    selectionData:{
      type:Array,
      default:[{
        label:'test',
        value:0
      }]
    }
  },
  methods:{
    toggleDrop(e){
      this.$root.eventHub.$emit('selectBox',234)
      e.cancelBubble=true
      this.isDorp=true;
    },
    chooseSelection(index){
      this.selectVal=index;
      this.isDorp=false;
      this.$emit('on-change',this.selectionData[this.selectVal])   
    }
  },
  mounted(){
    this.$root.eventHub.$on('selectBox',(msg)=>{
        this.isDorp=false
    })
  }
}
</script>

<style scoped>
.selection-component {
  position: relative;
  display: inline-block;
}
.selection-show {
  border: 1px solid #e3e3e3;
  padding: 0 20px 0 10px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  height: 25px;
  line-height: 25px;
  border-radius: 3px;
  background: #fff;
}
.selection-show .arrow {
  display: inline-block;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid #e3e3e3;
  width: 0;
  height: 0;
  margin-top: -1px;
  margin-left: 6px;
  margin-right: -14px;
  vertical-align: middle;
}
.selection-list {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 25px;
  width: 100%;
  background: #fff;
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  z-index: 5;
}
.selection-list li {
  padding: 5px 15px 5px 10px;
  border-left: 1px solid #e3e3e3;
  border-right: 1px solid #e3e3e3;
  cursor: pointer;
  background: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

}
.selection-list li:hover {
  background: #e3e3e3;
}
</style>



