<template>
  <div>
    <this-dialog :is-shows="isShowCheckDialog" @on-close="checkStatus">
      请检查你的支付状态！
      <div class="button" @click="checkStatus">
        支付成功 
      </div>
      <div class="button" @click="checkStatus">
        支付失败
      </div>
    </this-dialog>
    <this-dialog :is-shows="isShowSuccessDialog" @on-close="toOrderList">
      购买成功！
    </this-dialog>
    <this-dialog :is-shows="isShowFailDialog" @on-close="toOrderList">
      购买失败！
    </this-dialog>   
  </div>
</template>

<script>
import Dialog from './base/dialog'
export default {
  components: {
    thisDialog: Dialog
  },
  props: {
     isShowCheckDialog:{
       type:Boolean,
       default:false
     },
     orderId:[Number,String]
  },
  data () {
    return {
      isShowSuccessDialog: false,
      isShowFailDialog: false,
    }
  },
  methods: {
    checkStatus(){
        this.$http.post('index', {orderId:this.orderId}).then((res) => {
           this.isShowSuccessDialog=true
           this.$emit('on-close-check-dialog')
        }, (error) => {
            this.isShowFailDialog=true
            alert('支付失败')
            this.$emit('on-close-check-dialog')
        })
    },
    toOrderList(e){
      this.isShowSuccessDialog=false;
      this.$router.push({path:'/orderList'})
      this.$emit('on-close-check-dialog')
    },
    callbackZhifu(){

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



